.button-random {
  background-color: #b6efa2;
  width: 15rem;
  padding: 0.8rem 0;
  color: #1d2025;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  gap: 1.6rem;
  align-items: center;
  font-size: 1.2rem;
  cursor: pointer;
}

.fetching {
  background-color: #9fb3c8;
  pointer-events: none;
  cursor: not-allowed;
}

.button-icon,
.button-text {
  font-size: 1.6rem;
  font-weight: 600;
}

.spinning .button-icon {
  animation-name: spin !important;
  animation-duration: 800ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
