.main-menu-container {
  margin: 0 auto;
  width: fit-content;
  display: flex;
  gap: 3.2rem;
  justify-content: space-between;
  color: #d9e2ec;
  margin-bottom: 8.2rem;
}

.main-menu-random-box {
  padding: 3.2rem;
  background-color: #2e445b;
  border-radius: 9px;
}

.main-menu-generations {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.2rem;
  margin-bottom: 3.2rem;
}

.main-menu-search-box {
  padding: 3.2rem;
  background-color: #2e445b;
  border-radius: 9px;
}

.button-random {
  margin: 0 auto;
}
