.header-txt-big {
  font-size: 1.6rem;
  color: #d9e2ec;
}

.header-txt {
  font-size: 1.2rem;
  color: #d9e2ec;
  text-align: center;
}

.version {
  font-weight: 700;
}

.link:link,
.link:visited {
  display: inline-block;
  color: #d9e2ec;
  text-decoration: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  transition: all 0.3s;
}

.link:hover,
.link:active {
  border-bottom: 1px solid transparent;
}
