.button-gen {
  width: 10rem;
  padding: 0.8rem 0;
  color: #1d2025;
  font-size: 20rem;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  gap: 1.6rem;
  align-items: center;
  font-size: 1.2rem;
  cursor: pointer;
}

.active {
  background-color: #1ddecb;
}

.inactive {
  background-color: #9fb3c8;
}

.button-icon,
.button-text {
  font-size: 1.6rem;
  font-weight: 600;
}
