.poke-team-container {
  margin: 0 auto;
  width: fit-content;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  color: #d9e2ec;
  margin-bottom: 8.2rem;
  padding: 3.2rem;
  background-color: #2e445b;
  border-radius: 9px;
}

.poke-team-title {
  text-align: center;
  margin-bottom: 3.2rem;
}

.poke-team-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3.2rem;
}
