* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  background-color: #1d2025;
}

img {
  display: block;
}

body,
html {
  background-size: 100rem 100rem;
  font-family: "Montserrat", sans-serif;
}

body {
  color: #bcccdc;
}

.heading-primary {
  font-size: 5.2rem;
  line-height: 1.2;
  letter-spacing: -3px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.4rem;
}

.heading-secondary {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2.4rem;
}

/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2e445b;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #627d98;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b1becc;
}
