/* 1248px */
@media (max-width: 78em) {
  .logo-logo {
    text-align: center;
    width: 50rem;
  }
  .header-txt {
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

/* 928px */
@media (max-width: 58em) {
  .poke-team-cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* 720px */
@media (max-width: 45em) {
  html {
    font-size: 50%;
  }
}

/* 640px */
@media (max-width: 40em) {
  .header {
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
  }
}

/* 544px */
@media (max-width: 34em) {
  html {
    font-size: 45%;
  }
}

/* 480px */
@media (max-width: 30em) {
  html {
    font-size: 35%;
  }
}

/* 384px */
@media (max-width: 24em) {
  html {
    font-size: 30%;
  }
}
