.poke-card {
  background-color: #627d98;
  border-radius: 9px;
  padding: 2.4rem;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.pokemon-name {
  text-align: center;
}

.pokemon-img {
  width: 20rem;
}

.pokemon-type-container {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  margin: 0 auto;
}

.pokemon-type-img {
  width: 6.4rem;
}
